* {
  font-family: 'Varela Round';
}

.body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
ul>li {
  list-style: none;
  font-weight: 900;
}
.navbar {
  width:100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  text-align: right;
  direction: rtl;
  background-color: rgb(180, 180, 180);
  box-shadow: 0px 0px 8px 2px #292828;
  // position: fixed;
  // top: 0;
  // width: 100%;
}
p {
  margin: 0px 40px;
}
a {
  text-decoration: none;
}
.nav_list>li {
  position: relative;
  margin: 18px 0;
}

.nav_list>li>a {
  color: rgb(8, 8, 8);
  text-align: center;
}

.nav_list>li>a p {
  text-transform: uppercase;
}

.nav_list>li>a>.link {
  font-size: 2rem;
  transition: opacity 300ms ease-in-out;
}

.nav_list>li>a .hidden_link {
  position: absolute;
  z-index: 10;
  left: 30%;
  top: 50%;
  transform: translate(-50%, -40%);
  color: rgb(17, 16, 16);
  text-align: center;
  font-size: 16px;
  opacity: 0;
  letter-spacing: 50px;
  pointer-events: none;
}

.nav_list>li>a:hover>.link {
  opacity: 0.3;
}

.nav_list>li>a:hover>.hidden_link {
  animation: show-link 400ms ease-in-out forwards;
}
@keyframes show-link {
  to {
  opacity: 1;
  letter-spacing: 5px;
  pointer-events: all;
  }
  }
 


@media screen and (max-width: 1000px) {
  .navbar {
    display: none;
    //flex-direction: column;
  }
  
  .menu-btn{
    display: block;
    position: absolute;
    z-index: 2;
    right: 20px;
    /*left: 20px; */
    top: 20px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;
    font-size: 20px;
    cursor: pointer;
    /*color: #fff;*/
    /*background: linear-gradient(90deg, #f92c78, #4114a1);*/
    /* background: linear-gradient(375deg, #1cc7d0, #2ede98); */
   /* background: linear-gradient(-45deg, #e3eefe 0%, #efddfb 100%); */
    transition: all 0.3s ease-in-out;
  }
  .menu-btn span,
  .menu-btn:before,
  .menu-btn:after{
    content: "";
    position: absolute;
    top: calc(50% - 1px);
    left: 30%;
    width: 40%;
    border-bottom: 2px solid #000;
    transition: transform .6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .menu-btn:before{
    transform: translateY(-8px);
  }
  .menu-btn:after{
    transform: translateY(8px);
  }
  
  
  .close {
    z-index: 1;
    width: 100%;
    height: 100%;
    pointer-events: none;
    transition: background .6s;
  }
  
  /* closing animation */
  #active:checked + .menu-btn span {
    transform: scaleX(0);
  }
  #active:checked + .menu-btn:before {
    transform: rotate(45deg);
    border-color: #fff;
  }
  #active:checked + .menu-btn:after {
    transform: rotate(-45deg);
    border-color: #fff;
  }
  .wrapper ul{
    position: absolute;
    top: 60%;
    left: 80%;
    height: 90%;
    transform: translate(-50%, -50%);
    list-style: none;
    text-align: center;
  }
  .wrapper ul li{
    height: 10%;
    margin: 15px 0;
  }
  .wrapper ul li a{
    text-decoration: none;
    font-size: 25px;
    font-weight: 500;
    padding: 5px 30px;
    color: #fff;
    border-radius: 50px;
    position: absolute;
    // line-height: 50px;
    overflow: hidden;
    margin: 5px 30px;
    opacity: 0;
    transition: all 0.3s ease;
    transition: transform .6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .wrapper ul li a:after{
    position: absolute;
    content: "";
    background: #fff;
     /*background: linear-gradient(#14ffe9, #ffeb3b, #ff00e0);*/
    /*background: linear-gradient(375deg, #1cc7d0, #2ede98);*/
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 50px;
    transform: scaleY(0);
    z-index: -1;
    transition: transform 0.3s ease;
  }
  // .wrapper ul li a:hover:after{
  //   // transform: scaleY(1);
  // }
  .wrapper ul li a:hover{
    color: #974900;
  }
  input[type="checkbox"]{
    display: none;
  }
  .content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    text-align: center;
    width: 100%;
    color: #202020;
  }
  .content .title{
    font-size: 40px;
    font-weight: 700;
  }
  .content p{
    font-size: 35px;
    font-weight: 600;
  }
  
  #active:checked ~ .wrapper ul li a{
    opacity: 1;
  }
  .wrapper ul li a{
    transition: opacity 1.2s, transform 1.2s cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translateX(100px);
  }
  #active:checked ~ .wrapper ul li a{
    transform: none;
    transition-timing-function: ease, cubic-bezier(.1,1.3,.3,1); /* easeOutBackを緩めた感じ */
     transition-delay: .6s;
    transform: translateX(-100px);
  }
}
.wrapper{
  position: fixed;
  top: 0;
  /*left: -100%;*/
  right: -100%;
  height: 100%;
  width: 100%;
  background: rgba(95, 92, 92, 0.815);
  /*background: linear-gradient(90deg, #f92c78, #4114a1);*/
  /* background: linear-gradient(375deg, #1cc7d0, #2ede98); */
 /* background: linear-gradient(-45deg, #e3eefe 0%, #efddfb 100%);*/
  transition: all 0.6s ease-in-out;
}
#active:checked ~ .wrapper{
  /*left: 0;*/
  right:0;
}
#active{
 display: none;
}


@keyframes show-link {
  to {
    opacity: 1;
    letter-spacing: 5px;
    pointer-events: all;
  }
}
