.pie{  
        width: 450px;
        height:450px;
        margin:100px auto;
        overflow:hidden;
       position:relative;
}
.h1{
    font-size: 50px;
    text-shadow: 2px 7px 5px rgba(0,0,0,0.3), 
    0px -4px 10px rgba(255,255,255,0.3);
    font-style: italic;
}
.p{
    direction: rtl;
    margin:100px auto;
}
.allCount{
    display: grid;
    grid-template-columns: 50% 50%;
    justify-items: center; 
    padding: 10px;
   
}
.allCount div{
    width: 350px;
    height: 350px;
    background-color: rgba(158, 158, 158, 0.356);
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    justify-content: space-around;
    font-size: 70px;
    border: solid #a7700a 2px;
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.034),
        0 6.7px 5.3px rgba(0, 0, 0, 0.048),
        0 12.5px 10px rgba(0, 0, 0, 0.06),
        0 22.3px 17.9px rgba(0, 0, 0, 0.072),
        0 41.8px 33.4px rgba(0, 0, 0, 0.086),
        0 100px 80px rgba(0, 0, 0, 0.12);
}
.allCount div p{
    font-size: 30px;
    
  
}
@media (max-width:790px) {
    .allCount div{
        width: 280px;
        height: 280px;
        border-radius: 25px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column;
        justify-content: space-around;
        font-size: 45px;
        
    }
    .allCount{  
        align-items: center;
        padding: 10px;
        display: flex;
        flex-flow: column;
        justify-items: space-around;
        padding: 20px;  
    }
    .notes{
        margin-top: 150px;
    }
    
    
}